import { initial } from 'lodash';
import { ARTIFACT_GROUP_ID, AS_ADMIN_NAMESPACE_NAME } from './lib/constants';
import { Elevations } from './lib/Elevations';
import { PagingMethods } from './lib/PagingMethods';

const constants = {
  ARTIFACT_GROUP_ID,
  AS_ADMIN_NAMESPACE_NAME,
  DEFAULT_REQUEST_QUERY_OBJECT_PROPERTY_NAME: 'query',
  PagingMethods,
  Elevations,
};

const segmentCount = (path: string): number => path.split('.').length;

const parentPathOf = (path: string): string =>
  initial(path.split('.')).join('.');

const lastPathSegmentOf = (path: string): string | undefined =>
  path.split('.').pop();

export { constants, segmentCount, parentPathOf, lastPathSegmentOf };
